/* jshint ignore:start */

if (!runningTests) {
  require("dummy/app")["default"].create({"name":"ember-smooch","version":"latest"});
}

define('~fastboot/app-factory', ['dummy/app', 'dummy/config/environment'], function(App, config) {
  App = App['default'];
  config = config['default'];

  return {
    'default': function() {
      return App.create(config.APP);
    }
  };
});


